.vgl-wave-wrap {
  position: relative; }
  .vgl-wave-wrap .vgl-lettr {
    position: relative; }
    .vgl-wave-wrap .vgl-lettr .vgl-lettr-inner {
      position: absolute;
      transition: all 0.15s ease-out;
      bottom: 0; }
    .vgl-wave-wrap .vgl-lettr.vgl-active .vgl-lettr-inner {
      bottom: 20%; }
