.vgl-ghost-wrap {
  position: relative;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap; }
  .vgl-ghost-wrap .vgl-ghost-original, .vgl-ghost-wrap .vgl-ghost-bold {
    transition: all 0.3s;
    display: inline-block;
    position: absolute; }
  .vgl-ghost-wrap .vgl-ghost-original {
    opacity: 0.6; }
  .vgl-ghost-wrap .vgl-ghost-bold {
    transform: scale(1.1) translateX(10px) translateY(-10px) rotate(4deg);
    transition: transform 0.3s, opacity 0.3s;
    opacity: 0; }
    .vgl-ghost-wrap .vgl-ghost-bold.vgl-active {
      opacity: 1;
      transform: scale(1) translateX(0px) translateY(0px) rotate(0deg); }
