.vgl-rollup-wrap {
  position: relative;
  display: inline-block;
  vertical-align: top;
  overflow: hidden; }
  .vgl-rollup-wrap .vgl-rollup-original, .vgl-rollup-wrap .vgl-rollup-bold {
    transition: all 0.3s;
    display: inline-block;
    position: absolute; }
  .vgl-rollup-wrap .vgl-rollup-bold {
    font-weight: bold; }
