.vgl-rotate-wrap {
  position: relative;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap; }
  .vgl-rotate-wrap .vgl-rotate-original, .vgl-rotate-wrap .vgl-rotate-bold {
    display: inline-block;
    position: absolute; }
  .vgl-rotate-wrap .vgl-rotate-original {
    background: grey;
    transition: all 0.4s; }
  .vgl-rotate-wrap .vgl-rotate-bold {
    transform: rotateY(-90deg);
    transition: all 0.4s;
    background-color: black;
    color: white; }
  .vgl-rotate-wrap.vgl-active {
    perspective: 800px;
    transform-style: preserve-3d; }
    .vgl-rotate-wrap.vgl-active .vgl-rotate-original {
      transform: rotateY(-90deg); }
    .vgl-rotate-wrap.vgl-active .vgl-rotate-bold {
      transform: rotateY(0deg); }
