.vgl-swing-wrap {
  position: relative;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap; }
  .vgl-swing-wrap .vgl-swing-original, .vgl-swing-wrap .vgl-swing-bold {
    display: inline-block; }
  .vgl-swing-wrap .vgl-swing-original {
    transform-origin: 0% 50%;
    transform-style: preserve-3d; }
  .vgl-swing-wrap .vgl-swing-bold {
    position: absolute;
    transform: rotateX(270deg);
    transition: transform 0.6s;
    transform-origin: 0 0;
    pointer-events: none;
    background-color: black;
    color: white; }
  .vgl-swing-wrap.vgl-active {
    perspective: 800px; }
    .vgl-swing-wrap.vgl-active .vgl-swing-bold {
      transform: rotateX(0deg); }
